export class PageExecuteCmdController {
    static $inject = ["$stateParams", "materialUtils"];

    key: string;
    showSivenav: Function;

    constructor(private $stateParams: ng.ui.IStateParamsService, private materilUtils: fx.utils.materialStatic) {
        this.key = $stateParams["key"];

        this.showSivenav = this.materilUtils.buildToggle("executeCmdRight");
    }
}


